@font-face {
  font-family: gumela;
  src: url('https://fonts.cdnfonts.com/s/22720/Gumela.woff');
}
body {
  margin: 0;
  font-family: 'gumela', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.btn{
  border: none;
  padding: 15px 30px;
  background-color: orange;
  border: 2px solid orange;
  font-size: 18px;
  color: white;
  cursor: pointer;
}
.btn:hover{
  border: 2px solid orange;
  padding: 15px 30px;
  background-color: rgb(255, 255, 255);
  font-size: 18px;
  color: orange;
}
