header{
    display: flex;
    height: 15vh;
    padding: 0px 30px 0px 30px;
    position: relative;
    align-items: center;
    justify-content: space-between;
    background: rgb(255,180,91);
background: linear-gradient(90deg, rgba(255,180,91,1) 0%, rgba(108,70,1,1) 85%);
}
.footer{
    display: flex;
    width: 100%;
    height: 20vh;
    background-color: rgb(55, 55, 55);
    color: white;
    align-items: center;
    justify-content: center;
}
.h-left{
    text-align: center;
    align-items: center;
}
.h-left span{
font-size: 30px;
font-weight: bold;
background: -webkit-linear-gradient(#483804, #de8b35);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.h-left img{
    width: 65px;
}
.h-left a{
    text-decoration: none;
}
.h-right ul{
    display: flex;
    flex-direction: row;
    justify-content: end;
    
}
.h-right ul > li{
    padding: 6px 20px 6px 20px;
    list-style-type: none;
    color: white;
    font-weight: 600;
    font-size: 20px;
    cursor: pointer;
}
nav ul li a{
    text-decoration: none;
    color: white;
}
nav ul li :hover{
    text-decoration: none;
    /* border: 1px solid orange; */
    padding: 4px 15px;
    border-radius: 10px;
    background-color: white;
    color: orange;
}