.section-content{
    display: flex;
    width: 100%;
    height: auto;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    padding: 24px 0;
    text-align: center;
  }
  .primary-section{
    width: 50%;
  }
  .primary-section img{
    width: 650px;
  }
  .secondary-section{
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    width: 50%;
    padding: 25px 50px;
  }
  .secondary-section span{
    padding: 15px 0;
    font-size: 18px;
    
  }
  .secondary-section label{
    text-align: left;
    font-size: 20px;
    padding-left: 5px;
    margin-top: 8px;
  }
  .secondary-section input{
    padding: 12px 10px;
    margin: 5px 0;
    /* height: 30px; */
  }
  .secondary-section button{
    margin-top: 10px;
  }