.main{
    height: 580px;
    background-image: url("../../images/bg-h.jpg");
    width: 100%;
    background-repeat: no-repeat;
    background-size: contain;
    position: relative;
}
.bg-color{
    width: 100%;
    height: 100%;
    position: relative;
    background-color: #43370fc2;
    display: flex;
    flex-direction: column;
}
.main-img{
    text-align: center;
    width: 100%;
    margin-top: 34px;
}
.main-img img{
    width: 300px;
    /* padding: 10px; */
    border-radius: 100%;
    border: 5px solid #e0b770;
}
.main-desc{
    width: 100%;
    height: auto;
    text-align: center;
}
.main-desc span{
  font-family: 'gumela';
    font-size: 60px;
    font-weight: 700;
}
.main-desc :nth-child(1){
    color: white;
}
.main-desc :nth-child(2){
    color: orange;
}
.main-desc p{
    padding: 0 300px;
    font-size: 20px;
    line-height: 1.65;
    color: white;
    font-weight: 400;
}
.section{
    padding: 100px 100px;
}
.section-heading{
    font-size: 20px;
    color: orange;
}
.section-heading span{
    color: black;
    font-size: 40px;
    font-weight: bold;
}
.about-desc{
    width: 100%;
    height: auto;
    display: flex;
    padding-top: 80px;
    flex-direction: row;
    position: relative;
    text-align: center;
    align-items: center;
}
.desc-img{
    width: 50%;
    text-align: center;
    
}
.desc-img img{
    width: 400px;
    padding: 5px;
    border: 2px solid orange;
}
.desc-details{
    width: 50%;
    text-align: justify;
    line-height: 1.63;
}
.text{
    margin-bottom: 0px;
    float: left;
    font-size: 18px;
}
.text p i{
    color: orange;
}
.about-short{
    width: 100%;
    height: auto;
    clear: both;
    float: left;
    margin-bottom: 24px;
  }
  .about-short ul{
    margin: 0;
    list-style-type: none;
  }
  .about-short ul li{
    margin: 0 0 10px;
    width: 50%;
    float: left;
    position: relative;
    /* padding-left: 30px; */
  }
  .about-short ul li label{
    font-weight: bold;
  }
  .portfolio {
    width: 100%;
    height: 30vh;
    background-color: black;
  }
  .port-main{
    color: white;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0 150px;
    height: 100%;
    align-items: center;
  }
  .port-content{
    align-items: center;
    position: relative;
    display: flex;
    flex-direction: column;
  }
  .port-content label{
    font-size: 28px;
  }
  .port-content span{
    font-size: 24px;
  }
  